// common/http.interceptor.js

import config from '@/common/config.js';

const codeMessage = {
	404: '',
	500: '',
};

const install = (Vue, vm) => {

	//  https://www.uviewui.com/js/http.html
	Vue.prototype.$u.http.setConfig({
		baseUrl: config.baseUrl, // 
		method: '',
		// jsonJSON.parse()
		dataType: '',
		showLoading: true, // loading
		loadingText: '', // loading
		loadingTime: 800, // ms
		originalData: true, // 
		loadingMask: true, // loading
		// 
		header: {},
	});

	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// token
		// vuextokenuViewvuex
		// https://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorage
		// Storage
		const token = uni.getStorageSync('token');
		config.header.Authorization = token;
		// config.header.Token = 'xxxxxx';

		// urlurlthis.$u.get(url)url
		if (config.url == '/user/login') config.header.noToken = true;
		// configreturn
		return config;
		// returnfalse
		// if(config.url == '/user/rest') return false; // 
	}

	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {

		// if 
		if (res.statusCode == 200) {
			let result = res.data;
			// if 
			if (result.code == 200) {
				return result.result || result.data;
			} else if (result.code == 300) {
				vm.$u.toast('!');
			} else {
				vm.$u.toast(result.desc);
			}
		} else {
			let errorMessage = codeMessage[res.statusCode] ? codeMessage[res.statusCode] : res.statusCode;
			vm.$u.toast(errorMessage);
		}
		return false;
	}
}

export default {
	install
}
