import Vue from 'vue';
import config from '@/common/config.js';
let vm = new Vue();

export default {
	loginFun() {
		wx.getUserProfile({
			desc: '',
			success: userPro => {
				wx.login({
					success: function(login_res) {
						wx.request({
							url: config.baseUrl + '/user/login',
							method: 'POST',
							data: {
								code: login_res.code, //
								rawData: userPro.rawData, //
								signature: userPro.signature, //
								encrypteData: userPro.encryptedData, //
								iv: userPro.iv //
							},
							success: function(res) {
								if (res.statusCode == 200) {
									uni.setStorageSync('userInfo', res.data);
									vm.$u.vuex("emx_userInfo", res.data);
									vm.$u.vuex("emx_uid", res.data.id);
									uni.setStorageSync('token', res.header
										.Authorization);
									vm.$u.vuex("emx_token", res.header
										.Authorization);
									uni.setStorageSync('skey', res.data.skey);
									vm.$u.vuex("emx_hasLogin", true)
									uni.showToast({
										title: ''
									});
								} else if (res.statusCode == 500) {
									vm.$u.vuex("emx_hasLogin", false);
									uni.showToast({
										title: '',
										icon: "none",
										duration: 5000
									});
								} else {
									vm.$u.vuex("emx_hasLogin", false);
									uni.showToast({
										title: '',
										icon: "none",
										duration: 3000
									});
								}
							},
							fail: function(error) {
								vm.$u.vuex("emx_hasLogin", false);
								uni.showToast({
									title: '',
									icon: "none",
									duration: 3000
								});
							}
						})
					}
				});
			}
		})
	}

}
